//ÂÖ®Â±ÄDOMÂêéÊé•ÁöÑÊòØ‰∏Ä‰∏™ÂØπË±°Ôºõ
//ÂÖ®Â±ÄjQueryÂêéÊé•ÁöÑÊòØ‰∏Ä‰∏™ÂáΩÊï∞Ôºõ
window.jQuery = function (selectorOrArray) {//Êé•ÂèóselectorOrArrayÔºàÈÄâÊã©Âô®ÊàñÊï∞ÁªÑÔºâ
  let elements;//Áî≥Êòé‰∏Ä‰∏™Á©∫ÁöÑelements
  if(typeof selectorOrArray === 'string'){//Âà§Êñ≠ÔºöÂ¶ÇÊûúselectorOrArrayÁöÑÁ±ªÂûãÊòØ‰∏Ä‰∏™Â≠óÁ¨¶‰∏≤
    elements = document.querySelectorAll(selectorOrArray);//Ëé∑ÂèñÂà∞ÊâÄÊúâÂÖÉÁ¥†‰ª¨Âπ∂ËµãÂÄºÁªôelements
  }else if (selectorOrArray instanceof Array){//Âà§Êñ≠ÔºöÂ¶ÇÊûúselectorOrArrayÁöÑÁ±ªÂûãÊòØ‰∏Ä‰∏™Êï∞ÁªÑ
    elements = selectorOrArray; //Áõ¥Êé•Â∞ÜËøô‰∏™Êï∞ÁªÑËµãÂÄºÁªôelements
  }

//ÈúÄÊ≥®ÊÑèÔºöÊàë‰ª¨Ëé∑ÂèñÂà∞ÊâÄÊúâÁöÑÂÖÉÁ¥†Âêé‰∏çÊòØÁõ¥Êé•ËøîÂõûÊâÄÊúâÁöÑÂÖÉÁ¥†‰ª¨ÔºåËÄåÊòØËøîÂõû‰∏Ä‰∏™ÂØπË±°ÔºàÂèàÁß∞api);Ëøô‰∏™ÂØπË±°‰∏≠‰øùÂ≠òÂèØ‰ª•Áî®Êù•ÊìçÁ∫µÊâÄÊúâÂÖÉÁ¥†‰ª¨ÁöÑÊñπÊ≥ï
  //Ëøô‰πüÂ∞±ÂàöÂ•ΩÊûÑÊàê‰∫ÜÈó≠ÂåÖ
return {
    oldApi: selectorOrArray.oldApi,
  //ÁªôÂÖÉÁ¥†Ê∑ªÂä†classNameÂ±ûÊÄß
  addClass(className){
    //ÈÅçÂéÜËé∑ÂèñÂà∞ÂÖÉÁ¥†‰ª¨ÔºàelementsÔºâ
    for(let i = 0;i<elements.length;i++){
      //ÈÅçÂéÜÂæóÂà∞ÊØè‰∏Ä‰∏™ÂÖÉÁ¥†ÔºåÂØπÂÆÉËøõË°åÊ∑ªÂä†classNameÂ±ûÊÄß
      elements[i].classList.add(className);
    }
    return this;//thisÂ∞±ÊòØapi
  },
  //Êü•ÊâæÊâÄÊúâÂÖÉÁ¥†
  find(selector){//selectorÊòØÈÄâÊã©Âô®
    let array = [];//Áî≥Êòé‰∏Ä‰∏™Á©∫Êï∞ÁªÑÔºåÁî®‰∫éÊîæÊü•ÊâæÂà∞ÁöÑÂÖÉÁ¥†
    for(let i =0;i<elements.length;i++){//ÈÅçÂéÜËé∑ÂèñÂà∞ÁöÑÂÖÉÁ¥†‰ª¨ÔºàelementsÔºâ
      //ÈÅçÂéÜÂæóÂà∞ÊØè‰∏Ä‰∏™ÂÖÉÁ¥†ÔºåÁÑ∂ÂêéÂÜçÈÄöËøáquerySelectorAllÂéªËé∑ÂèñÂà∞ÂÆÉÂØπÂ∫îÁöÑÂ≠ê‰ª£ÂÖÉÁ¥†‰ª¨;ÂÖÉÁ¥†‰ª¨ÊòØÁî±‰∏Ä‰∏™‰º™Êï∞ÁªÑÁªÑÊàêÔºåÂÜçÈÄöËøá
      //Array.from()ËΩ¨Êç¢ÊàêÁúüÊ≠£ÁöÑÊï∞ÁªÑÔºõÊúÄÂêé‰∏éÁ©∫Êï∞ÁªÑÁõ∏ËøûÊé•Ôºàconcat()ÔºâÊãøÂà∞ÊâÄÊúâÁöÑÂÖÉÁ¥†
       array = array.concat(Array.from(elements[i].querySelectorAll(selector)));
    }
    array.oldApi = this;//thisÂ∞±ÊòØApi(api1)
    return jQuery(array);//ËøîÂõûÊñ∞ÁöÑapi(api2)ÔºåÁî®‰∫éÊìç‰Ωúarray
  },
  end(){
    return this.oldApi; //(thisÂ∞±ÊòØapi2)
  },
  //ÈÅçÂéÜelements
  each(fn) {//Êé•Âèó‰∏Ä‰∏™ÂèÇÊï∞fn(fnÊòØ‰∏Ä‰∏™ÂáΩÊï∞)
    for (let i = 0; i < elements.length; i++) {//ÈÅçÂéÜelements
      fn.call(null, elements[i], i)//ÈÅçÂéÜÂæóÂà∞elements‰∏≠ÁöÑÊØè‰∏Ä‰∏™ÂÖÉÁ¥†Âèä‰∏ãÊ†áÔºåÂπ∂‰º†Áªô‰Ω†fn
    }
    return this //thisÂ∞±ÊòØapi
  },
  //Êü•ÊâæÁà∏Áà∏
  parent() {
    let array = [];//Â£∞Êòé‰∏Ä‰∏™Á©∫ÂØπË±°Áî®‰∫éÊîæÂÖÉÁ¥†ÁöÑÁà∏Áà∏‰ª¨
    //ÊâæÂà∞ÊâÄÊúâÂ≠©Â≠ê‰ª¨ÁöÑÁà∏Áà∏ÔºåÂπ∂Ê∑ªÂä†Âà∞Êï∞ÁªÑ‰∏≠
    this.each(node =>{
      if(array.indexOf(node.parentNode) === -1){
        //Âà§Êñ≠ÔºöÂ¶ÇÊûúÊï∞ÁªÑ‰∏≠Ê≤°ÊúâÔºåÂ∞±Ê∑ªÂä†ËøõÂéª
        array.push(node.parentNode);
      }
    } );//thisÂ∞±ÊòØapi(‰πüÂèØ‰ª•ËØ¥ÊòØÂΩìÂâçreturnÁöÑËøô‰∏™ÂØπË±°Ôºå
    // Âõ†‰∏∫Ëøô‰∏™ÂØπË±°ÈáåÂ∞ÅË£Ö‰∫ÜÂØπelementsÊìçÁ∫µÁöÑÊâÄÊúâÊñπÊ≥ïÔºõÊâÄ‰ª•Êàë‰ª¨ÊääËøô‰∏™ÂØπË±°ÁªüÁß∞‰∏∫api)
    return jQuery(array);//ËøîÂõûapi
  },
  //ÊâìÂç∞Âá∫ÊâÄÊúâÁöÑÂÖÉÁ¥†
  print(){
      console.log(elements);
  },
  //ÊâæÊâÄÊúâÂ≠©Â≠ê‰ª¨
  children(){
      const array =[];//Â£∞Êòé‰∏Ä‰∏™Á©∫ÂØπË±°Áî®‰∫éÊîæÂÖÉÁ¥†ÁöÑÂ≠©Â≠ê‰ª¨
    //ÊâæÂà∞ÊâÄÊúâÂ≠©Â≠ê‰ª¨ÔºåÂπ∂Ê∑ªÂä†Âà∞Êï∞ÁªÑ‰∏≠
      this.each((node)=>{
        if (array.indexOf(node.children) === -1){//Âà§Êñ≠ÔºöÂ¶ÇÊûúÊï∞ÁªÑ‰∏≠Ê≤°ÊúâÔºåÂ∞±Ê∑ªÂä†ËøõÂéª
          array.push(...node.children)//'...'ÊòØÂ±ïÂºÄÊìç‰ΩúÁ¨¶
        }
      });
    return jQuery(array)//ËøîÂõûapi
  },
};
};